*{
    box-sizing: border-box;
  }
  
  html,
  body{
    width: 100%;
    height: 100%;
    margin: 0;
    background-color:white;
    font-family: 'Roboto' , sans-serifl
  }
  .full-contant{
    display: flex
  }
  .value-of-cart{
      width:25%;
      height: auto;
      margin: 5em 2em;
      background:white;
      flex-direction: column;
  }
  .shopping-cart{
    width: 95%;
    height: auto;
    margin: 5em 2em;
    background: #FFF;
    // display: flex;
    flex-direction: column;
  }
  
  /* item styling */
  .title{
    height: 2.75em;
    border-bottom: 1px solid #E1E8EE;
    padding: 1.25em 0.75em;
    color: #5E6977;
    font-size: 22px;
    line-height: 0.5;
    font-weight: 500;
  }
  .item{
    padding: 1.25em 1.75em;
    transition: width 2s;
    height: 7.5em;
    display: flex;
  }
  .item:nth-child(3) {
    border-top: 1px solid #E1E8EE;
    border-bottom: 1px solid #E1E8EE;
  }
 
  .buttons{
    position: relative;
    padding-top: 1.75em;
    margin-right: 3.5em;
  }
  .delete-btn,
  .like-btn{
    display: inline-block;
    cursor: pointer;
  }
  .delete-btn{
    width: 18px;
    height: 17px;
    background: url("https://designmodo.com/demo/shopping-cart/delete-icn.svg") no-repeat center;
  }
  .like-btn{
    position: absolute;
    top: 9px;
    left: 15px;
    background: url("https://designmodo.com/demo/shopping-cart/twitter-heart.png");
    width: 60px;
    height: 60px;
    background-size: 2900%;
    background-repeat: no-repeat;
  }
  
  .is-active{
    animation-name: animate;
    animation-duration: .8s;
    animation-iteration-count: 1;
    animation-timing-function: steps(28);
    animation-fill-mode: forwards;
  }
  @keyframes animate{
    0% {background-position: left;}
    50% {background-position: right;}
    100% {background-position: right;}
  }
  
  .image{
    margin-right: 50px;
  }
  .description{
    padding-top: 10px;
    margin-right: 60px;
    width: 115px;
  }
  .description span{
    display: block;
    font-size: 1em;
    color: #43484D;
    font-weight: 400;
  }
  .description span:first-child{
    margin-bottom: 5px;
  }
  .description span:last-child{
    font-weight: 300;
    margin-top: 8px;
    color: #86939E;
  }
  
  .quantity{
    padding-top: 20px;
    margin-right: 60px;
  }
  .quantity input{
    -webkit-appearance: none;
    border: none;
    text-align: center;
    width: 32px;
    font-size: 1em;
    color: #43484D;
    font-weight: 300;
  }
  button[class*=btn] {
    width: 30px;
    height: 30px;
    background-color: #E1E8EE;
    border-radius: 6px;
    border: none;
    cursor: pointer;
  }
  .minus-btn img{
    margin-bottom: 3px;
  }
  .plus-btn img{
    margin-top: 2px;
  }
  
  button:focus,
  input:focus{
    outline: 0;
  }
  
  .total-price{
    width: 83px;
    padding-top: 27px;
    text-align: center;
    font-size: 1em;
    color: #43484D;
    font-weight: 300;
  }
  
  .subtotal-title{
    color: #111111cf;
    padding-right: 8px;
    font-size: 17px;
    font-weight: 500;
  }
  .total-title{
    color:black;
    padding-right: 8px;
    font-size: 18px;
    font-weight: 700;
  }
  .grand-total{
      margin-left: 20px;
  }
  .css-fk6mpy {
    display: block;
}
.css-fk6mpy {
   
    border-width: 0px 0px 1px;
    border-top-style: initial;
    border-right-style: initial;
    border-left-style: initial;
    border-top-color: initial;
    border-right-color: initial;
    border-left-color: initial;
    border-image: initial;
    border-bottom-style: solid;
    border-bottom-color: rgb(229, 229, 229);
    margin-block-start: 0px;
    margin-block-end: 0px;
    margin-top: 15px;
}


button.btn{
  width: 91%;
    background: transparent;
    color: #B22222;
    border: 2px solid #B22222;
    letter-spacing: 2px;
    text-transform: uppercase;
    cursor: pointer;
    display: inline-block;
    margin: 13px 17px;
    transition: all 0.4s;
}
button.btn-1:hover{
  background-color: #B22222;
  color: white;
  -webkit-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
}
  /* media queries */
  @media (max-width: 800px) {
    .shopping-cart{
      width: 75%;
      height: 500px;
      overflow: hidden;
    }
    .item {
      height: 500px;
      flex-wrap: wrap;
      justify-content: center;
    }
    .image img{
      width: 50%;
    }
    .image,
    .quantity,
    .description{
      width: 100%;
      text-align: center;
      margin: 6px 0;
    }
    .buttons{
      margin-right: 20px;
    }
  }